.error-container {
    display: flex;
    justify-content: center;
    text-align: center;
}

.loading {
    display: flex;
    margin: 10px;
    justify-content: center;
}

.nodata-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

body {
    min-width: 320px;
    margin: 0;
    font-family: sans-serif;
    font-size: 16px;
}
h1 {
    font-size: 2em;
}
.header {
    padding-top: 1em;
    margin-top: 0px;
    text-align: center;
    /* background-color: #666666; */
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    color: transparent;
    text-shadow: rgba(255, 255, 255, 1) 0px 3px 3px;
}
.app {
    width: 100%;
    min-height: 100vh;
    background-image: -webkit-gradient(
        linear,
        left top, right top,
        from(#e4afcb),
        color-stop(0%, #b8cbb8),
        color-stop(0%, #b8cbb8),
        color-stop(30%, #e2c58b),
        color-stop(64%, #c2ce9c),
        to(#7edbdc)
    );
    background-image: linear-gradient(
        to right,
        #e4afcb 0%,
        #b8cbb8 0%,
        #b8cbb8 0%,
        #e2c58b 30%,
        #c2ce9c 64%,
        #7edbdc 100%
    );
}

.search-form {
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-bar {
    width: 50%;
    border: none;
    padding: 10px;
}

.search-button {
    background: cornflowerblue;
    border: none;
    padding: 10px 20px;
    color: white;
    cursor: pointer;
}

.recipes {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 95%;
}
.recipe {
    width: 40%;
    border-radius: 10px;
    box-shadow: 0em 0.3em 1.3em grey;
    margin: 1.5em;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    padding: 0.3em;
}

.weight {
    color: blue;
    font-weight: 800;
}
.image {
    border-radius: 10%;
    width: 90%;
    height: auto;
}
/* ///////////////////////////////////////////// MEDIA */
@media only screen and (max-width: 1000px) {
    body {
        font-size: 15px;
    }
}
@media only screen and (max-width: 800px) {
    body {
        font-size: 14px;
    }
    .recipe {
        width: 80%;
        padding: 1em;
        border: 1px solid black;
    }
}
@media only screen and (max-width: 600px) {
    body {
        font-size: 12px;
    }
}
@media only screen and (max-width: 500px) {
    body {
        font-size: 10px;
    }
    .recipe {
        width: 80%;
        padding: 1em;
        border: 1px solid black;
    }
}

